import React from "react";

import Grid from "@mui/material/Unstable_Grid2";
import Box from "@mui/material/Box";
import Modal from "@mui/material/Modal";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import ShieldOutlinedIcon from "@mui/icons-material/ShieldOutlined";
import CardMedia from "@mui/material/CardMedia";
import "animate.css";

const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: "70%",
  bgcolor: "#d4d4d4",
  boxShadow: 24,
  p: 4,
};

export default function Battle({ pokemonData, isShiny, listOfPokemons }) {
  const [open, setOpen] = React.useState(false);
  const [enemyPokemonData, setEnemyPokemonData] = React.useState(null);

  const [isAttacking, setIsAttacking] = React.useState(false);

  const [win, setWin] = React.useState(0);
  const [lose, setLose] = React.useState(0);

  const [animationAppearEnemy, setAnimationAppearEnemy] = React.useState("");

  const [friendHp, setFriendHp] = React.useState(
    pokemonData?.stats[0].base_stat
  );
  const [enemyHp, setEnemyHP] = React.useState(null);

  const [friendDefense] = React.useState(pokemonData?.stats[2].base_stat);
  const [enemyDefense, setEnemyDefense] = React.useState(null);

  const [friendAttack] = React.useState(
    pokemonData?.stats[1].base_stat - enemyDefense
  );
  const [enemyAttack, setEnemyAttack] = React.useState(
    enemyPokemonData?.stats[1].base_stat - friendDefense
  );

  const [friendInfoBox, setFriendInfoBox] = React.useState("");
  const [enemyInfoBox, setEnemyInfoBox] = React.useState("");

  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  //minus hp Enemy when Attack friend
  const handleAttack = () => {
    setIsAttacking(true);
    setEnemyHP((prevEnemyHp) => prevEnemyHp - friendAttack);
    if (enemyHp - friendAttack <= 0) {
      setEnemyHP(0);
      setWin(win + 1);
      if (lose === lose + 1) {
        return lose;
      }
      fetchEnemyPokemon();
    }

    setFriendHp((prevFriendHp) => prevFriendHp - enemyAttack);
    if (friendHp - enemyAttack <= 0) {
      setLose(lose + 1);

      setFriendInfoBox(`HIT ${friendHp - (friendDefense - enemyAttack)}`);
      setEnemyInfoBox(`HIT ${enemyHp - (enemyDefense - enemyAttack)}`);
      setTimeout(() => {
        setFriendHp(pokemonData?.stats[0].base_stat);
        setFriendInfoBox("");
        setEnemyInfoBox("");

        fetchEnemyPokemon();
        setIsAttacking(false);
      }, 2000);
    } else {
      fetchEnemyPokemon();
      setIsAttacking(false);
    }
  };

  const getRandomPokemon = () => {
    const randomIndex = Math.floor(Math.random() * listOfPokemons.length);
    const randomPokemon = listOfPokemons[randomIndex];
    return randomPokemon;
  };

  const fetchEnemyPokemon = () => {
    const randomPokemonName = getRandomPokemon();
    fetch(`https://pokeapi.co/api/v2/pokemon/${randomPokemonName}`)
      .then((response) => response.json())
      .then((pokemonData) => {
        setEnemyPokemonData(pokemonData);
        setEnemyHP(pokemonData?.stats[0].base_stat);
        setEnemyAttack(pokemonData?.stats[1].base_stat);
        setEnemyDefense(pokemonData?.stats[2].base_stat);
        setAnimationAppearEnemy("animate__slideInRight");
      })

      .catch((error) => {
        console.error("Error fetching enemy Pokémon:", error);
      });
  };

  const handleBattle = () => {
    fetchEnemyPokemon();
    handleOpen();
  };

  return (
    <div>
      <Button
        variant="contained"
        color="warning"
        size="large"
        onClick={handleBattle}
      >
        &nbsp;&nbsp;&nbsp;
        <ShieldOutlinedIcon />
        &nbsp;&nbsp; BATTLE &nbsp;&nbsp;
      </Button>
      <Modal
        keepMounted
        open={open}
        onClose={handleClose}
        aria-labelledby="keep-mounted-modal-title"
        aria-describedby="keep-mounted-modal-description"
        style={{ backdropFilter: "blur(5px)" }}
      >
        <Box sx={style}>
          <Box sx={{ width: "100%" }}>
            <Grid
              container
              rowSpacing={1}
              columnSpacing={{ xs: 1, sm: 2, md: 3 }}
            >
              <Grid xs={6}>
                <Typography
                  variant="h5"
                  sx={{
                    minWidth: 175,
                    textAlign: "center",
                    fontFamily: "LogoFontMain",
                  }}
                >
                  <p style={{ color: "green", fontWeight: "bolder" }}>
                    WIN: {win}{" "}
                  </p>
                </Typography>
              </Grid>
              <Grid xs={6}>
                <Typography
                  variant="h5"
                  sx={{
                    minWidth: 175,
                    textAlign: "center",
                    fontFamily: "LogoFontMain",
                  }}
                >
                  <p style={{ color: "red", fontWeight: "bolder" }}>
                    LOSE: {lose}
                  </p>
                </Typography>{" "}
              </Grid>
              <Grid xs={4} sx={{ textAlign: "center" }}>
                <Typography variant="h8" sx={{ fontWeight: "bolder" }}>
                  {pokemonData?.name?.toUpperCase()}
                </Typography>
                <Typography variant="body1" sx={{ fontWeight: "bolder" }}>
                  HP: {friendHp}
                </Typography>
                <CardMedia
                  component="img"
                  sx={{
                    width: "80%",
                    height: "80%",
                    objectFit: "cover",
                    transform: "scaleX(-1)",
                  }}
                  image={
                    isShiny
                      ? pokemonData?.sprites?.other["official-artwork"]
                          ?.front_shiny
                      : pokemonData?.sprites?.other["official-artwork"]
                          ?.front_default
                  }
                  title="Pokemon Image"
                />
              </Grid>
              <Grid xs={2}>
                <Typography variant="h5" sx={{ marginTop: "100%" }}>
                  {friendInfoBox}
                </Typography>
              </Grid>
              <Grid xs={2}>
                <Typography variant="h5" sx={{ marginTop: "100%" }}>
                  {enemyInfoBox}
                </Typography>
              </Grid>
              <Grid xs={4} sx={{ textAlign: "center" }}>
                <Typography variant="h8" sx={{ fontWeight: "bolder" }}>
                  {enemyPokemonData?.name?.toUpperCase()}
                </Typography>
                <Typography variant="body1" sx={{ fontWeight: "bolder" }}>
                  HP: {enemyHp}
                </Typography>
                <CardMedia
                  className={`animate__animated ${animationAppearEnemy}`}
                  component="img"
                  sx={{
                    width: "80%",
                    height: "80%",
                    objectFit: "cover",
                  }}
                  image={
                    isShiny
                      ? enemyPokemonData?.sprites?.other["official-artwork"]
                          ?.front_shiny
                      : enemyPokemonData?.sprites?.other["official-artwork"]
                          ?.front_default
                  }
                  title="Pokemon Image"
                />
              </Grid>
              <Grid xs={12} sx={{ textAlign: "center" }}>
                <Button
                  variant="contained"
                  color="error"
                  onClick={handleAttack}
                  disabled={isAttacking}
                >
                  {isAttacking ? "Attacking..." : "ATTACK"}
                </Button>
              </Grid>
            </Grid>
          </Box>
        </Box>
      </Modal>
    </div>
  );
}
